.page {
  scroll-behavior: smooth;
  background-color: $main-background-color;

  font-family: Poppins, sans-serif;
  font-size: 16px;
  line-height: 140%;

  &__body {
    margin: 0;
  }

  &__menu {
    pointer-events: none;


    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    transform: translateX(-100%);
    opacity: 0;

    transition: all 0.3s;

    &:target {
      pointer-events: all;
      transform: translateX(0);
      opacity: 1;
      z-index: 10;
    }
  }
}
